// pull in config vars
import * as config from '../config'
import getJamFilledCookie from '../getJamFilledCookie'

export default async (threadId) => {
    try {
        const res = await fetch(`${config.API_HOSTNAME}/api/plugin/jam/${config.JAM_ID}/threads/${threadId}/delete`, {
            method: 'POST',
            headers: { 
                'Content-Type': 'application/json', 
                'jam-filled-cookie': getJamFilledCookie()
            }
        })

        const json = await res.json()

        return json
    } catch (e) {
        console.log(e);
    }
}