// returns true if click is off jam and on the page
// goal is to allow people to interact with the page and also with jam 

export default (e) => {    
    // clickX/Y relative to viewport
    // https://stackoverflow.com/questions/6073505/what-is-the-difference-between-screenx-y-clientx-y-and-pagex-y
    const { clientX, clientY } = e

    let elementsToCheckIfClicked = []

    let clickOnJam = false

    // query selector get all open comments
    document.querySelectorAll('.jam-editor').forEach((newComment) => elementsToCheckIfClicked.push(newComment))

    // get all previews
    document.querySelectorAll('.jam-preview').forEach((preview) => elementsToCheckIfClicked.push(preview))

    // get all open threads
    document.querySelectorAll('.thread-header-container').forEach((thread) => elementsToCheckIfClicked.push(thread))
    document.querySelectorAll('.thread-container').forEach((thread) => elementsToCheckIfClicked.push(thread))

    // get the jam button
    elementsToCheckIfClicked.push(document.querySelector('.jam---main-jam-button'))


    // check if click is on any of the jam elements
    elementsToCheckIfClicked.forEach((element) => {
        const elementPosition = element.getBoundingClientRect()
        const checkX = clientX > elementPosition.left && clientX < elementPosition.right
        const checkY = clientY > elementPosition.top && clientY < elementPosition.bottom
        if (checkX && checkY) {
            clickOnJam = true
        }        

        // for testing - put a box around each elemement
        // const el = document.createElement('div')
        // el.style.position = 'absolute'
        // el.style.zIndex = 2000000
        // el.style.background = 'blue'
        // el.style.opacity = 0.1
        // el.style.top = `${elementPosition.y}px`
        // el.style.height = `${elementPosition.height}px`
        // el.style.left = `${elementPosition.x}px`
        // el.style.width = `${elementPosition.width}px`
        // document.body.appendChild(el)

    })
    return !clickOnJam
}