import reactionLeft from '../util/reactionLeft'
import JamEditor from './JamEditor'
import createNewThread from '../util/fetchApiHelpers/createNewThread'
import { EditorState, ContentState, convertToRaw } from 'draft-js'

export default class JamCreateNewComment extends React.Component {
    state = { editorState: EditorState.createEmpty() }

    onEditorChange = (editorState) => {
        this.setState({
            editorState,
        });
    }

    handleMessageSend = async () => {
        const { x, y, updateThread } = this.props
        const { editorState } = this.state
        const message = convertToRaw(editorState.getCurrentContent())
        const newThread = await createNewThread(x, y, JSON.stringify(message))
        await updateThread(newThread)
        // reset editor
        this.setState({ editorState: EditorState.push(editorState, ContentState.createFromText('')) })
    }

    render() {
        const { x, y, loggedInUser, mentions } = this.props
        const { editorState } = this.state
        return (
            <div style={{ top: `${y}px`, left: `${reactionLeft(x)}px` }} className="jam-editor">
                <div className='thread-editor-container'>
                    <div className="thread-header-container">
                        <div style={{ display: 'flex' }}>
                            <img className="thread-header-author-avatar" src={loggedInUser.avatar} />
                            <div className="thread-header-author-name">{loggedInUser.name}</div>
                        </div>
                        <div onClick={this.handleMessageSend} className="jam---button" >Send</div>
                    </div>
                    <JamEditor onEditorChange={this.onEditorChange} editorState={editorState} handleMessageSend={this.handleMessageSend} mentions={mentions} />
                </div>
            <style jsx>{`
                .thread-editor-container {
                    padding: 0
                }
                .thread-header-container div::selection, .thread-header-container img::selection {
                    outline: none
                }
            `}</style>
            </div>
        )
    }
}