import Mousetrap from 'mousetrap'

export default class KeyboardShortcuts extends React.Component {
    componentDidMount = () => {
        const { toggleJamMode, handleMessageSend } = this.props
        // bind the keyboard shortcuts

        // type j a m to turn on/off jam
        Mousetrap.bind('j a m', () => { toggleJamMode() })

        // type cmd enter to send a comment
        Mousetrap.bind('command+enter', () => { 
            console.log("command enter") 
            handleMessageSend()
        }) 
    }
    render() {
        return <div></div>
    }
}