// according to https://codepulse.blog/simple-easy-way-display-draft-js-content/ 
// the best way to display content made in draftjs is just to show the editor in read only mode
// so that is what this is

import { Editor, EditorState, convertFromRaw } from "draft-js";
import createEmojiPlugin from 'draft-js-emoji-plugin';
import createLinkifyPlugin from 'draft-js-linkify-plugin';
import createMentionPlugin from 'draft-js-mention-plugin';
import MultiDecorator from "draft-js-plugins-editor/lib/Editor/MultiDecorator";
import { CompositeDecorator } from "draft-js";

const emojiPlugin = createEmojiPlugin();
const { EmojiSuggestions, EmojiSelect } = emojiPlugin;

const linkifyPlugin = createLinkifyPlugin();

const getPluginDecoratorArray = (plugins) => {
    let decorators = [];
    let plugin;
    // check each plugin that will be used in the editor for decorators
    for (plugin of plugins) {
        if (plugin.decorators !== null && plugin.decorators !== undefined) {
            // if the plugin has any decorators, add them to a list of all decorators from all plugins
            decorators = decorators.concat(plugin.decorators);
        }
    }
    
    return decorators;
}

const getAllPluginDecorators = (plugins) => {
    return new MultiDecorator(
        [new CompositeDecorator(getPluginDecoratorArray(plugins))]
    );
}

export default class JamEditorDisplayMode extends React.Component {
    state = { editorState: EditorState.createEmpty() }

    constructor(props) {
        super(props);
        this.mentionPlugin = createMentionPlugin()
        const plugins = [this.mentionPlugin, emojiPlugin, linkifyPlugin];
        this.decorators = getAllPluginDecorators(plugins)        
        if (props.rawMessageBlocks) this.state.editorState = EditorState.createWithContent(convertFromRaw(props.rawMessageBlocks), this.decorators);
    }
    render() {
        const plugins = [this.mentionPlugin, emojiPlugin, linkifyPlugin];
        return (
            <div className="comment-message">
                <Editor 
                    editorState={this.state.editorState} 
                    readOnly={true} 
                    decorators={this.decorators}
                    plugins={plugins} 
                    onChange ={(editorState) => this.setState({editorState})} 
                />
            </div>
        )
    }
}