import dynamic from 'next/dynamic'
dynamic( () => import('draft-js-mention-plugin/lib/plugin.css'), { ssr: false } )
dynamic( () => import('draft-js-emoji-plugin/lib/plugin.css'), { ssr: false } )
import { EditorState, RichUtils } from 'draft-js';
import Editor from 'draft-js-plugins-editor';
import createMentionPlugin, { defaultSuggestionsFilter } from 'draft-js-mention-plugin';
const editorStyles = dynamic( () => import('../public/jamDraftJsMentionsStyles.css'), { ssr: false })
import createEmojiPlugin from 'draft-js-emoji-plugin';
import createLinkifyPlugin from 'draft-js-linkify-plugin';
import createToolbarPlugin, { Separator } from 'draft-js-static-toolbar-plugin';
import { CodeBlockButton } from 'draft-js-buttons';
import {getDefaultKeyBinding, KeyBindingUtil} from 'draft-js';
const {hasCommandModifier} = KeyBindingUtil;

const myKeyBindingFn = (e) => {
  if (e.keyCode == 13 /* `cmd enter` key */ && hasCommandModifier(e)) {
    return 'jameditor-enter';
  }
  return getDefaultKeyBinding(e);
}

const mentionsTheme = {
    mention: 'mention',
    mentionSuggestions: 'mentionSuggestions',
    mentionSuggestionsEntry: 'mentionSuggestionsEntry',
    mentionSuggestionsEntryFocused: 'mentionSuggestionsEntryFocused',
    mentionSuggestionsEntryText: 'mentionSuggestionsEntryText',
    mentionSuggestionsEntryAvatar: 'mentionSuggestionsEntryAvatar',
    mentionSuggestionsEntryContainer: 'mentionSuggestionsEntryContainer',
    mentionSuggestionsEntryContainerRight: 'mentionSuggestionsEntryContainerRight',
    mentionSuggestionsEntryText: 'mentionSuggestionsEntryText',
    mentionSuggestionsEntryTitle: 'mentionSuggestionsEntryTitle'
}

const emojiPlugin = createEmojiPlugin({ 
  priorityList: {
    ':hot_pepper:': ["1F336"],
    ':tada:': ["1F389"],
    ':fire:': ["1F525"],
    ':100:': ["1f4af"],
    ':strawberry:': ["1F353"],
    ':unicorn:': ["1F984"],
    ':joy_cat:': ["1F639"],
    ':ghost:': ["1F47B"],
    ':hear_no_evil:': ["1F649"]
  },
  useNativeArt: true
});
const { EmojiSuggestions, EmojiSelect } = emojiPlugin;

const linkifyPlugin = createLinkifyPlugin();

const staticToolbarPlugin = createToolbarPlugin();
const { Toolbar } = staticToolbarPlugin;

const Entry = (props) => {
    const {
      mention,
      theme,
      searchValue, // eslint-disable-line no-unused-vars
      isFocused, // eslint-disable-line no-unused-vars
      ...parentProps
    } = props;
  
    return (
      <div {...parentProps}>
        <div className={theme.mentionSuggestionsEntryContainer}>
          <div className={theme.mentionSuggestionsEntryContainerLeft}>
            <img
              src={mention.avatar}
              className={theme.mentionSuggestionsEntryAvatar}
              role="presentation"
            />
          </div>
  
          <div className={theme.mentionSuggestionsEntryContainerRight}>
            <div className={theme.mentionSuggestionsEntryText}>
              {mention.name}
            </div>
  
            <div className={theme.mentionSuggestionsEntryTitle}>
              {mention.title}
            </div>
          </div>
        </div>
      </div>
    );
  };

export default class JamEditor extends React.Component {

    constructor(props) {
      super(props);

      this.state.readOnly = false

      this.mentionPlugin = createMentionPlugin({
        mentions: props.mentions,
        entityMutability: 'IMMUTABLE',
        theme: mentionsTheme,
        mentionPrefix: '@',
        supportWhitespace: true
      });
    }
  
    state = {
      editorState: EditorState.createEmpty(),
      suggestions: this.props.mentions,
      readOnly: false
    };
  
    onChange = (editorState) => {
        this.props.onEditorChange(editorState)
    };
  
    onSearchChange = ({ value }) => {
      const { mentions } = this.props
      this.setState({
        suggestions: defaultSuggestionsFilter(value, mentions),
      });
    };
  
    onAddMention = () => {
      // get the mention object selected
    }
  
    focus = () => {
      this.editor.focus();
    };

    handleKeyCommand = (command, editorState) => {
      if (command === 'jameditor-enter') {  
        this.setState({ readOnly: true })
        this.props.handleMessageSend()
        return 'handled'
      }
        const newState = RichUtils.handleKeyCommand(editorState, command);
        if (newState) {
          this.onChange(newState);
          return 'handled';
        }
        return 'not-handled';
    }
  
    render() {
      const { MentionSuggestions } = this.mentionPlugin;
      const { readOnly } = this.state
      const { editorState } = this.props
      const plugins = [this.mentionPlugin, emojiPlugin, linkifyPlugin, staticToolbarPlugin];
  
      return (
        <div className={`${editorStyles.editor} jam-editor-draftjs-wrapper`} onClick={this.focus}>
          <Editor
            editorState={editorState}
            handleKeyCommand={this.handleKeyCommand}
            onChange={this.onChange}
            plugins={plugins}
            ref={(element) => { this.editor = element; }}
            placeholder="Type a comment..."
            keyBindingFn={myKeyBindingFn}
            readOnly={readOnly}
          />
          <MentionSuggestions
            onSearchChange={this.onSearchChange}
            suggestions={this.state.suggestions}
            onAddMention={this.onAddMention}
            entryComponent={Entry}
          />
          <EmojiSuggestions />
          <Toolbar>
            {(externalProps) => (
              <React.Fragment>
                <CodeBlockButton {...externalProps} />
              </React.Fragment>
            )}
          </Toolbar>
          <style jsx>{`
            .jam-editor-draftjs-wrapper {
                width: 100%;
            }
            `}</style>
            <style jsx global>{`
                .DraftEditor-root {
                    width: 100%;
                    box-sizing: border-box;
                    padding: 0 1rem 1rem 1rem;
                    word-break: break-all;
                }
            `}</style>
        </div>
      );
    }
  }