// pull in config vars
import * as config from '../config'
import getJamFilledCookie from '../getJamFilledCookie'
import getUserIdFromJamFilledCookie from '../getUserIdFromJamFilledCookie'

export default async () => {
    try {
        const res = await fetch(`${config.API_HOSTNAME}/api/user/${getUserIdFromJamFilledCookie()}`, {
            headers: { 
                'Content-Type': 'application/json', 
                'jam-filled-cookie': getJamFilledCookie(),
                'jamid': config.JAM_ID
            }
        })
                
        const json = await res.json()

        localStorage.setItem('jamUser', JSON.stringify(json.user))
        
        return true
    } catch (e) {
        console.log(e);
        
        return false
    }
}