// called when no cookie in the browser, so need to get cookie 
// when redirected back with cookie, that is set in the dom

import { JAM_ID, API_HOSTNAME } from './config'

export default () => {
    // save the current url to be able to redirect them back to it
    const jamURLBeforeAuth = window.location.href
    localStorage.setItem('jamURLBeforeAuth', jamURLBeforeAuth)

    // direct user to login
    window.location = `${API_HOSTNAME}?action=login&jamId=${JAM_ID}&redirect-uri=${jamURLBeforeAuth}`
}