export default class ClickCollectorOverlay extends React.Component {
    handleClick = (e) => {
        const { handleClickOnClickCollector } = this.props        
        handleClickOnClickCollector(e)
    }

    handleDoubleClick = (e) => {
        const { handleDoubleClickOnClickCollector } = this.props        
        handleDoubleClickOnClickCollector(e)
    }

    render() {
        return (
            <div onClick={this.handleClick} onDoubleClick={this.handleDoubleClick} className="click-collector"></div>
        )
    }
}