// pull in config vars
import * as config from '../config'
import getJamFilledCookie from '../getJamFilledCookie'
import getUserIdFromJamFilledCookie from '../getUserIdFromJamFilledCookie'

export default async ( x, y, message ) => {
    try {
        const res = await fetch(`${config.API_HOSTNAME}/api/plugin/jam/${config.JAM_ID}/threads`, {
            method: 'POST',
            headers: { 
                'Content-Type': 'application/json', 
                'jam-filled-cookie': getJamFilledCookie()
            },
            body: JSON.stringify({
                author: getUserIdFromJamFilledCookie(), 
                x,
                y,
                jamId: config.JAM_ID,
                firstMessage: message
            })
        })

        const json = await res.json()

        // returns updated thread object
        return json
    } catch (e) {
        return false
    }
}

