import reactionLeft from '../util/reactionLeft'
import humanReadableDaysAgo from '../util/humanReadableTimeAgo'
import { EditorState, convertFromRaw } from 'draft-js'

export default class JamPreview extends React.Component {
    constructor(props) {
        super(props)
        this.avatars = this.createAvatarsArray()
        this.previewText = this.createMessagePreview()
    }

    createMessagePreview = () => {
        const { thread } = this.props
        const jsonifiedCommentMessage = JSON.parse(thread.comments[thread.comments.length - 1].message)
        const messageConvertedFromRaw = convertFromRaw(jsonifiedCommentMessage)    
        const tempEditorState = EditorState.createWithContent(messageConvertedFromRaw)
        let commentText = tempEditorState.getCurrentContent().getPlainText('\u0001')
        return commentText.replace('\u0001', ' ')
    }

    createAvatarsArray = () => {
        const { thread } = this.props
        let authorsArray = []
        thread.comments.forEach((comment) => {
            // if not alreadt in the authors aray, add it
            if (!authorsArray.some(author => author.name === comment.author.name) && authorsArray.length < 6) {
                authorsArray.push(comment.author)
            } 
        })

        return authorsArray.map((author) => <img key={author._id} src={author.avatar} />)
    }
    render() {
        const { thread, handlePreviewClick } = this.props
        return (
            <div onClick={handlePreviewClick} style={{ top: `${thread.y}px`, left: `${reactionLeft(thread.x)}px` }} id={`preview-thread-${thread._id}`} className={`jam-preview ${thread._id}`}>
                <div className="preview-container">
                    {/* <div className="preview-notification"></div> */}
                    <div className="preview-avatars">{this.avatars}</div>
                    <div className="preview-num-comments">{`${thread.comments.length} ${thread.comments.length > 1 ? "replies" : "comment"}`}</div>
                    <p className="preview-text">{this.previewText}</p>
                    <div className="preview-reply-date">{humanReadableDaysAgo(thread.comments[thread.comments.length - 1]._id)}</div>
                </div>
            </div>
        )
    }
}