// pull in config vars
import * as config from './config'
import getJamFilledCookie from './getJamFilledCookie'
import getUserIdFromJamFilledCookie from './getUserIdFromJamFilledCookie'

export default async ( threadId, message ) => {
    try {
        const res = await fetch(`${config.API_HOSTNAME}/api/plugin/jam/${config.JAM_ID}/members`, {
            method: 'GET',
            headers: { 
                'Content-Type': 'application/json', 
                'jam-filled-cookie': getJamFilledCookie()
            }
        })

        const json = await res.json()

        console.log(json);

        // returns updated thread object
        return json.members
    } catch (e) {
        return false
    }
}

