import Head from 'next/head'
import JamButton from '../components/JamButton'
import KeyboardShortcuts from '../components/KeyboardShortcuts'
import isAuthenticated from '../util/isAuthenticated'
import isPermissioned from '../util/isPermissioned'
import logInUser from '../util/logInUser'
import getUserIdFromJamFilledCookie from '../util/getUserIdFromJamFilledCookie'
import getUserByIdInJamFilledStorage from '../util/fetchApiHelpers/getUserByIdInJamFilledStorage'
import getUserFromLocalStorage from '../util/getUserFromLocalStorage'
import getThreads from '../util/fetchApiHelpers/getThreads'
import JamThread from '../components/JamThread'
import JamCreateNewComment from '../components/JamCreateNewComment'
import ClickCollectorOverlay from '../components/ClickCollectorOverlay'
import { toast, Slide } from 'react-toastify'
import TypeJamToEnableJamBanner from '../components/TypeJamToEnableJamBanner'
import getPeopleInTheJam from '../util/getPeopleInTheJam'
import demoUsers from '../util/demoUsers'
import clickIsNotOnJamElement from '../util/fetchApiHelpers/clickIsNotOnJamElement'

// pull in config vars
import * as config from '../util/config' 

export default class Index extends React.Component {

    static getInitialProps = ({ query }) => {
        return { jamFilledCookie: query['jam_filled_cookie'] }
    }

    static getDerivedStateFromProps = (props, state) => {
        // if cookie is in the query param, save it as a cookie
        return { jamFilledCookie: props.jamFilledCookie }
    }

    state = {
        ready: false,
        jamFilledCookie: '',
        clickX: null,
        clickY: null,
        jamTime: false,
        newCommentEditorOpen: false,
        threads: [],
        whichThreadIsOpen: -1, // thread index with open preview since only one can be open at a time. -1 is all closed
        hasPermission: false // check for permission for user to jam on this Jam
    }

    componentDidMount = async () => {
        const { jamTime, jamFilledCookie } = this.state 

        // if cookie is in the query param, save it as a cookie and in local storage 
        if (jamFilledCookie) {
            document.cookie = `jam_filled_cookie=${jamFilledCookie}, domain=${window.location.host}`
            await getUserByIdInJamFilledStorage()
            window.location.search = ''
        } 

        // if demo, load in demo users
        if (window.location.host == 'letsjam.xyz' || window.location.host == 'dani.town') {
            // choose a demo user
            let selectedDemoUserIndex = Math.floor(Math.random() * 10)
            document.cookie = `jam_filled_cookie=${demoUsers[selectedDemoUserIndex].token}`
            localStorage.setItem('jamUser', JSON.stringify(demoUsers[selectedDemoUserIndex]))
        }   

        if (jamTime) {
            await this.fetchInitialJamTimeData()
        }

        // some toast configuration
        if (toast) toast.configure({
            autoClose: 5000,
            position: "top-center",
            pauseOnHover: true,
            hideProgressBar: true,
            transition: Slide,
            toastClassName: 'jam---toast'
        })
    }

    fetchInitialJamTimeData = async () => {
        // check users permission and auth
        await this.handleAuthentication()

        // fetch initial threads
        this.setState({ threads: await getThreads() }, ()=> { this.setState({ ready: true }) })

        // get the people in the jam for mentions in the editor
        this.setState({ mentions: await getPeopleInTheJam() })
    }

    handleClick = (e) => {
        const { whichThreadIsOpen, newCommentEditorOpen } = this.state

        if (clickIsNotOnJamElement(e)) {
            // if editor is open, close it
            if (newCommentEditorOpen) {
                this.setState({ newCommentEditorOpen: false })
            } else if (whichThreadIsOpen > -1) {
                // else if a thread is open, close it
                this.setState({ whichThreadIsOpen: -1 })
            } 
        }
    }

    handleDoubleClick = (e) => {
        const { whichThreadIsOpen, newCommentEditorOpen } = this.state
        if (clickIsNotOnJamElement(e)) {
            // if editor is open, close it
            if (newCommentEditorOpen) {
                this.setState({ newCommentEditorOpen: false })
            } else if (whichThreadIsOpen > -1) {
                // else if a thread is open, close it
                this.setState({ whichThreadIsOpen: -1 })
            } else {
                // else create a new commment
                this.createNewComment(e)
            }
        }
    }

    toggleJamMode = async () => {
        const newJamMode = !this.state.jamTime
        
        this.setState({ jamTime: newJamMode })
        //  if jam time, make sure user is authed and has permissions
        if (newJamMode) await this.handleAuthentication().then(() => {
            // tell the user how to jam
            this.notify("Hey! Youre in jam mode. Double click to add a comment.")
        })

        // if first jam mode, go fetch all the things
        if (newJamMode && !this.state.threads.length) await this.fetchInitialJamTimeData()

        // click event handler
        if (newJamMode) {
            document.addEventListener('click', this.handleClick)
            document.addEventListener('dblclick', this.handleDoubleClick)
        } else {
            document.removeEventListener('click', this.handleClick)
            document.removeEventListener('dblclick', this.handleDoubleClick)
        }
    }

    handleAuthentication = async () => {
        const authenticated = isAuthenticated() // check if jam_filled_cookie present        
        if (authenticated) {
            // if the user is in local storage, get user and set them to local storage
            if (!getUserFromLocalStorage()) {
                await getUserByIdInJamFilledStorage()
            }

            // then get user from local storage
            this.setState({ loggedInUser: getUserFromLocalStorage() })

            // check permissions - can they access this jam?
            const permissioned = await isPermissioned()
            this.setState({ hasPermission: permissioned })

            // if no, need to redirect them to request permission
            if (!permissioned) window.open(`${config.API_HOSTNAME}?state=request-access&jamId=${config.JAM_ID}&userId=${getUserIdFromJamFilledCookie}`, '_blank');
        } else {
            // go and get authenticated!
            const loginSuccess = await logInUser() // sets user to local storage
            // set user to state
            if (loginSuccess) this.setState({ loggedInUser: getUserFromLocalStorage() })
            else { console.log('failed to login') }
        }
    }

    createNewComment = (e) => {
        this.setState({ newCommentEditorOpen: true })
        this.setState({ clickX: e.pageX, clickY: e.pageY })
    }

    updateThread = (newThread) => {
        // this is called after a comment is posted, and when thread is resolved, to update the thread in the ui
        const { threads } = this.state
        let tempThreads = threads
        const index = threads.findIndex(thread => thread._id == newThread._id)
        index > -1 ? tempThreads[index] = newThread : tempThreads.push(newThread)
        this.setState({ threads: tempThreads, tempThreadIndexForUpdateThreadFunction: index }, () => {
            // to keep the thread open after commenting
            const { tempThreadIndexForUpdateThreadFunction, threads } = this.state
            if (tempThreadIndexForUpdateThreadFunction == -1) this.updateWhichThreadIsOpen(threads.length - 1)
            else this.updateWhichThreadIsOpen(tempThreadIndexForUpdateThreadFunction)
        })
        
        // if newComment open, close it
        this.setState({ newCommentEditorOpen: false })
    }

    // used for resolving threads
    deleteThread = (threadId) => {
        try {
            const { threads } = this.state
            let tempThreads = threads
            const index = threads.findIndex(thread => thread._id == threadId)
            tempThreads.splice(index, 1)
            this.setState({ threads: tempThreads })
            this.updateWhichThreadIsOpen(-1)
        } catch (e) {
            console.log(e);
            
        }
        
    } 

    updateWhichThreadIsOpen = (threadId) => {
        this.setState({ whichThreadIsOpen: threadId })
    }

    notify = (msg) => {
        toast(msg)
    }

    render() {
        const { ready, jamTime, loggedInUser, threads, whichThreadIsOpen, newCommentEditorOpen, clickX, clickY, mentions } = this.state

        return (
            <div>
                {/* pull in jam css */}
                <Head>
                    <meta charSet="utf-8" />
                    <link rel="stylesheet" href="jam.css" />
                    <link rel="stylesheet" href="jam-comment.css" />
                    <link rel="stylesheet" href="thread.css" />
                    <link rel="stylesheet" href="thread-editor.css" />
                    <link rel="stylesheet" href="https://cdnjs.cloudflare.com/ajax/libs/draft-js/0.11.5/Draft.min.css" />
                    <link rel="stylesheet" href="jamDraftJsMentionsStyles.css" />
                    <link rel="stylesheet" href="https://cdn.jsdelivr.net/npm/react-toastify@5.5.0/dist/ReactToastify.css" />
                </Head>
                {/* load the jam button */}
                <JamButton toggleJamMode={this.toggleJamMode} jamTime={jamTime} />

                {/* load the little type jam to enable jam banner */}
                <TypeJamToEnableJamBanner jamTime={jamTime} />

                {/* when ready, and in jam mode, load threads and the click collector */}
                {ready && jamTime && threads.map((thread, index) => <JamThread key={thread._id} thread={thread} loggedInUser={loggedInUser} updateThread={this.updateThread} threadOpen={whichThreadIsOpen == index ? true : false} updateWhichThreadIsOpen={this.updateWhichThreadIsOpen} threadIndex={index} deleteThread={this.deleteThread} mentions={mentions} /> )}

                {/* if newCommentEditorOpen, add the new comment editor at state x and y */}
                {ready && jamTime && newCommentEditorOpen && <JamCreateNewComment loggedInUser={loggedInUser} x={clickX} y={clickY} updateThread={this.updateThread} mentions={mentions} />}
                
                {/* pull in keyboard shortcuts */}
                <KeyboardShortcuts toggleJamMode={this.toggleJamMode} />
            </div>
        )
    }
}